import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

/* 操作数据 */
const state = {
    count: 0
}
const handle = {
    add(state){
        state++;
    }
}
var vuexStore = new Vuex.Store({
    state,
    handle
});

// 使用 export default 封装，让外部可以访问
console.log(vuexStore);
export default vuexStore;
// 可以导出，方便调用



