//es6 的import的引入的方法，用来进行没模块的引入，是懒加载
import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
import VueResource from 'vue-resource';
Vue.use(VueResource);
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
// import './element-variables.scss'
Vue.use(ElementUI);
// 引入store.js文件
import storeConfig from './component/js/store'
// 引入echarts
import echarts from 'echarts';
Vue.prototype.$echarts = echarts;
//引入vue-quill-editor编辑器
import VueQuillEditor from 'vue-quill-editor'

import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
// /* 引入animate */

//Vue.use(),参数是import的变量名，用来进行注册
Vue.use(VueQuillEditor /* { default global options } */ );
import router from './router'
// Vue.use(Vuex);
//这里是进行的根实例的构建
//引用一个全局的js文件
import CM from './component/js/base';
Vue.prototype.$cm = CM;
//这里是new vue.config的位置
Vue.config.debug = true;
//引入状态文件
// import store from './component/js/store';
//定义组件既可以使用引入一个对象，也可以使用在这里同意配置的方式
new Vue({
  el: '#app',
  /* 运行时侯建的方式使用render的函数，而不是template的简单字符串的编译 */
  render: h => h(App),
  router,
  storeConfig
}).$mount('#app');



