<template>
  <div class="wrapper">
    <div class="charts_title">
      <p class="dataTitle">{{title}}</p>
    </div>
    <div id="charts">
    </div>
  </div>
</template>
<script>
let myChart;
var date_30 = new Date();
// console.log(getTime);
export default {
  name: "charts",
  data() {
    return {
      //显示内容的标题
      title: this.getData.search_content,
      //三种图表的切换
      type: 0,
      //请求的数据的公共的参数
      params: {
        start_time: "2018-01-02",
        end_time: "2018-02-02",
        show_type: 1,
        faq_id: ""
      },
      //缓存下来的getData
      save_props: {},
      //公共的options配置项
      options: {
        title: {
          text: "统计数据图",
          show: false
        },
        backgroundColor: "#ffffff",
        color: ["#4fb4ff", "#ff6060", "#4fb4ff", "#ff6060"],
        // animationEasing: "elasticOut",
        // animationDelayUpdate: function(idx) {
        //   return idx * 5;
        // },
        tooltip: {
          trigger: "axis",
          textStyle: {
            color: "#999999"
          },
          axisPointer: {
            type: "line",
            lineStyle: {
              color: "#d2ecff"
            }
          },
          backgroundColor: "#ffffff",
          borderColor: "#e5e5e5",
          borderWidth: 1,
          padding: [14, 18],
          extraCssText: "border-radius:2px 10px 2px 2px "
        },
        legend: {
          right: "20"
        },
        grid: {
          left: "1%",
          right: "1%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          axisLine: {
            show: false
          }
        },
        yAxis: {
          type: "value",
          minInterval: 1,
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            margin: 20
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: "dashed",
              color: ["#f5f5f5"]
            }
          }
        }
      }
    };
  },
  props: ["getData"], //标题,类型，时间数组
  watch: {
    save_props: {
      handler(cur) {
        this.params.faq_id = cur.faq_id;
        this.params.start_time = cur.start_time;
        this.params.end_time = cur.end_time;
        this.params.show_type = cur.type;
        this.type = cur.chart_type;
        this.init_charts(this.params);
      },
      deep: true
    },
    params: {
      handler(cur, old) {
        // console.log(cur, "子组件的getdata");
        // this.init_charts(cur);
      },
      deep: true
    },
    type(cur) {
      // this.init_charts(this.params);
    },
    all_options: {}
  },
  created() {
    this.save_props = this.getData;
    this.params.start_time = this.save_props.start_time;
    this.params.end_time = this.save_props.end_time;
    // console.log("首次加载", this.params);
  },
  mounted() {
    myChart = this.$echarts.init(document.getElementById("charts"));
    window.addEventListener("resize", function() {
      myChart.resize();
    });
    this.init_charts(this.params);
  },
  methods: {
    init_charts(cur) {
      // console.log(cur, "进入init分发");
      var type = this.type;
      //初始化数据根据参数显示
      this.$cm.ajax(
        "POST",
        "Faq.faq_totalshow",
        this.params,
        res => {
          // console.log(res);
          this.all_options = res.data.result;
          // console.log(this.options, this.all_options);
          this.options.xAxis.data = this.all_options.x;
          myChart.clear();
          switch (type) {
            case 1:
              this.assess();
              break;
            case 2:
              this.persional();
              break;
            default:
              this.drawLine();
              break;
          }
        },
        this
      );
    },
    drawLine(cur) {
      this.title = this.getData.search_content
        ? `【${this.getData.search_content}】访问量`
        : "全局访问量";
      //绘制折线图
      let series = this.all_options.visitTimes;
      let name = this.title;
      // console.log(this.all_options, 5666);
      const options0 = {
        color: ["#4fb4ff", "#4fb4ff", "#4fb4ff", "#4fb4ff"],
        legend: {
          data: ["访问量"]
        },
        grid: {
          left: "1%",
          right: "3%",
          bottom: "3%",
          containLabel: true
        },
        yAxis: {
          type: "value",
          minInterval: 1,
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            margin: 20
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: "dashed",
              color: ["#f5f5f5"]
            }
          }
        },
        series: [
          {
            name: "访问量",
            type: "line",
            itemStyle: {
              normal: {
                shadowBlur: 10,
                shadowColor: "#ccc"
              }
            },
            lineStyle: {
              width: 1
            },
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "#eaf6ff" // 0% 处的颜色
                  },
                  {
                    offset: 0.1,
                    color: "#eaf6ff" // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "#f7fbff" // 100% 处的颜色
                  }
                ],
                globalCoord: false // 缺省为 false
              }
            },
            data: series
          }
        ]
      };      
      this.replace_options(options0, this.all_options);
    },
    assess(cur) {
      this.title = this.getData.search_content
        ? `【${this.getData.search_content}】用户评价`
        : "全局用户评价";
      let series0 = this.all_options.likeTimes;
      let series1 = this.all_options.disLikeTimes;
      let series3 = this.all_options.likePercent;
      let series4 = this.all_options.disLikePercent;
      const options1 = {
        color: ["#4fb4ff", "#ff6060", "#4fb4ff", "#ff6060"],
        legend: {
          data: ["点赞次数", "点踩次数", "点赞率(%)", "点踩率(%)"]
        },
        yAxis: [
          {
            type: "value",
            minInterval: 1,
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              margin: 20
            },
            splitLine: {
              show: true,
              lineStyle: {
                type: "dashed",
                color: ["#f5f5f5"]
              }
            }
          },
          {
            type: "value",
            max: 100,
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              margin: 20,
              show: true,
              interval: "auto",
              formatter: "{value} %"
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: ["#f5f5f5"]
              }
            }
          }
        ],
        series: [
          {
            name: "点赞次数",
            type: "bar",
            barWidth: 10,
            barGap: "0",
            data: series0
          },
          {
            name: "点踩次数",
            type: "bar",
            barWidth: 10,
            barGap: "0",
            data: series1
          },
          {
            name: "点赞率(%)",
            lineStyle: {
              width: 1
            },
            itemStyle: {
              normal: {
                shadowBlur: 10,
                shadowColor: "#ccc"
              }
            },
            type: "line",
            yAxisIndex: 1,
            data: series3
          },
          {
            name: "点踩率(%)",
            lineStyle: {
              width: 1
            },
            itemStyle: {
              normal: {
                shadowBlur: 10,
                shadowColor: "#ccc"
              }
            },
            type: "line",
            yAxisIndex: 1,
            data: series4
          }
        ]
      };
      this.options.legend.data = options1.legend.data;
      this.options.series = options1.series;
      this.options.yAxis = options1.yAxis;
      // console.log(this.options, "可以被初始化了");
      // myChart.setOption(this.options);
      this.replace_options(options1);
    },
    persional(cur) {
      this.title = this.getData.search_content
        ? `【${this.getData.search_content}】转人工情况`
        : "全局转人工情况";
      let series0 = this.all_options.peopleTimes;
      let series1 = this.all_options.peoplePercent;
      const options = {
        legend: {
          data: ["转人工次数", "转人工占比(%)"]
        },
        color: ["#4fb4ff", "#4fb4ff", "#4fb4ff", "#4fb4ff"],
        yAxis: [
          {
            type: "value",
            minInterval: 1,
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              margin: 20
            },
            splitLine: {
              show: true,
              lineStyle: {
                type: "dashed",
                color: ["#f5f5f5"]
              }
            }
          },
          {
            type: "value",
            max: 100,
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              margin: 20,
              show: true,
              interval: "auto",
              formatter: "{value} %"
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: ["#f5f5f5"]
              }
            }
          }
        ],
        series: [
          {
            name: "转人工次数",
            type: "bar",
            barWidth: 10,
            data: series0
          },
          {
            name: "转人工占比(%)",
            type: "line",
            lineStyle: {
              width: 1
            },
            itemStyle: {
              normal: {
                shadowBlur: 10,
                shadowColor: "#ccc"
              }
            },
            yAxisIndex: 1,
            data: series1
          }
        ]
      };
      this.options.yAxis = options.yAxis;
      this.replace_options(options, this.all_options);
    },
    replace_options(cur_op) {
      // this.options = {};
      this.options.legend.data = cur_op.legend.data;
      this.options.series = cur_op.series;
      this.options.color = cur_op.color;
      console.log(this.options, "初始化了");
      myChart.setOption(this.options);
    }
  }
};
</script>
<style lang="less">
.wrapper {
  height: 100%;
  width: 100%;
}
.charts_title {
  width: 100%;
  padding: 6px 8px;
  .dataTitle {
    flex: 1;
  }
  .dataSelect {
    width: 60px;
    height: 30px;
    // text-align: right;
    .el-select {
      height: 100%;
    }
  }
}
#charts {
  height: 95%;
  width: 100%;
  overflow: hidden;
}
</style>




